import React from 'react';
import { Container } from '@material-ui/core';
import Page from 'src/components/Page';
import DetailConsignment from './DetailConsignment';
import { generalStyles } from '../utils/GeneralStyle';
import { useParams } from 'react-router';

const ConsignmentDetail = () => {
  const classes = generalStyles();
  const consignmentId = useParams();
  return (
    <Page className={classes.root} title={'Consignment Detail'}>
      <Container maxWidth={false}>
        <Container>
          <DetailConsignment />
        </Container>
      </Container>
    </Page>
  );
};

export default ConsignmentDetail;
