import React, { useEffect, useState } from 'react';
import { Box, Button, Card, CardContent, Grid, makeStyles, TextField, TableCell, TableRow } from '@material-ui/core';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import * as Yup from 'yup';
import GeneralPageTitleWithButton from 'src/views/utils/GeneralPageTitleWithButton';
import { useNavigate, useParams } from 'react-router';
import BaseTableHeader from 'src/views/utils/BaseTableHeader';
import GeneralTable from 'src/views/utils/GeneralTable';
import ConsignmentService from 'src/services/consignment';

const useStyles = makeStyles((theme) => ({
  root: {},
  buttons: {
    '& > *': {
      margin: theme.spacing(3),
    },
  },
  form: {
    width: '100%',
  },
}));

const DetailConsignment = () => {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState(false);
  const [consignmentInfo, setConsignmentInfo] = useState({});
  const [jobList, setJobList] = useState([]);
  const [inboundList, setInboundList] = useState([]);
  const [trackingList, setTrackingList] = useState([]);
  const noTablePagination = true;

  const { trackingNumber } = useParams();
  //Only when there is a ID in url, fetch data according to specific ID, otherwise, render an empty form.
  useEffect(() => {
    const fetchResults = async (id) => {
      setLoading(true);
      try {
        const result = await ConsignmentService.findDetailByTrackingNo(id);
        setConsignmentInfo(result.content.consignment);
        setJobList(result.content.jobinfo);
        setInboundList(result.content.inboundinfo);
        setTrackingList(result.content.trackinginfo);
      } catch (error) {
        enqueueSnackbar(error.toString(), { variant: 'error' });
      } finally {
        setLoading(false);
      }
    };

    trackingNumber && fetchResults(trackingNumber);
  }, [trackingNumber, enqueueSnackbar]);

  return (
    <Box>
      <Box mb={3}>
        <GeneralPageTitleWithButton title={'Consignment detail'} />
      </Box>
      <Box mb={3}>
          <GeneralTable
            baseTableHeader={
                <BaseTableHeader title="Consignment Info">
                </BaseTableHeader>
              }
              headers={[
                'TRACKING NUMBER',
                'REFERENCE',
                'SKU',
                'SUPPLIER',
                'WEIGHT',
                'DESCRIPTION'
              ]}
              data={consignmentInfo}
              loading={loading}
              noTablePagination={noTablePagination}
            >
              <TableRow hover key={consignmentInfo._id}>
                <TableCell>{consignmentInfo.trackingNumber}</TableCell>
                <TableCell>{consignmentInfo.referenceNumber}</TableCell>
                <TableCell>{consignmentInfo.SKU}</TableCell>
                <TableCell>{consignmentInfo.customerId}</TableCell>
                <TableCell>{consignmentInfo.weight}</TableCell>
                <TableCell>{consignmentInfo.DESCRIPTION}</TableCell>
              </TableRow>
            </GeneralTable>
            <GeneralTable
              baseTableHeader={
                <BaseTableHeader title="Job Info">
                </BaseTableHeader>
              }
              headers={['ID', 'JOB ID', 'SEQ.', 'CONSIGNMENT NO', 'STATUS', 'POSTCODE', 'ADDRESS1']}
              data={jobList}
              loading={loading}
              noTablePagination={noTablePagination}
            >
              {jobList?.map((jobInfo) => (
                <TableRow hover key={jobInfo._id}>
                    <TableCell>{jobInfo._id}</TableCell>
                    <TableCell>{jobInfo.jobId}</TableCell>
                    <TableCell>{jobInfo.sequence}</TableCell>
                    <TableCell>{jobInfo.consignmentNumber}</TableCell>
                    <TableCell>{jobInfo.status}</TableCell>
                    <TableCell>{jobInfo.postcode}</TableCell>
                    <TableCell>{jobInfo.address1}</TableCell>
                </TableRow>
                ))}
            </GeneralTable>
            <GeneralTable
              baseTableHeader={
                <BaseTableHeader title="Inbound Info">
                </BaseTableHeader>
              }
              headers={['CONSIGNMENT NO', 'STATUS', 'OPRATER ID', 'DATE']}
              data={inboundList}
              loading={loading}
              noTablePagination={noTablePagination}
            >
              {inboundList?.map((inboundInfo) => (
                <TableRow hover key={inboundInfo._id}>
            <TableCell>{inboundInfo.consignmentNumber}</TableCell>
                  <TableCell>{inboundInfo.status}</TableCell>
            <TableCell>{inboundInfo.operaterId}</TableCell>
                  <TableCell>{inboundInfo.date}</TableCell>
                </TableRow>
              ))}
            </GeneralTable>
            <GeneralTable
              baseTableHeader={
                <BaseTableHeader title="Tracking Info">
                </BaseTableHeader>
              }
              headers={['CONSIGNMENT NO', 'STATUS', 'SIGNATURE', 'DESCRIPTION', 'DATE']}
              data={trackingList}
              loading={loading}
              noTablePagination={noTablePagination}
            >
              {trackingList?.map((trackingInfo) => (
                <TableRow hover key={trackingInfo._id}>
                  <TableCell>{trackingInfo.consignmentNumber}</TableCell>
                  <TableCell>{trackingInfo.status}</TableCell>
                  <TableCell>{trackingInfo.signature}</TableCell>
                  <TableCell>{trackingInfo.description}</TableCell>
                  <TableCell>{trackingInfo.date}</TableCell>
                </TableRow>
              ))}
            </GeneralTable>
      </Box>
    </Box>
  );
};

export default DetailConsignment;
